.indiv-login {
	display: none;
	position: fixed;
	top: 40px;
	left: 0;
	width: 100%;
	height: calc(100% - 40px);
	background-color: $white;
	z-index: 50;
	&.is-show {
		display: block; }

	&__content {
		display: none;
		@include vcenter;
		top: 40%;
		flex-direction: column;
		width: 75%;
		padding: 50px;
		box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
		border-radius: 0 0 5px 5px;
		.section__subtitle {
			width: 300px; }
		&.is-show {
			display: flex; } } }
