body {
	@include fontRR;
	font-size: 16px;
	line-height: 1.4;
	background-color: $lightGray;
	&.is-open-popup, &.is-finish {
		position: fixed;
		width: 100%; }
	&.is-finish {
		.finish {
			display: block; } }
	.finish {
		display: none; }
	&.android {
		background-color: $white; } }

.out.preloader-show {
	position: fixed; }

a {
	text-decoration: none; }

.container {
	width: 1140px;
	margin: 0 auto;
	position: relative; }

[hidden] {
	display: none; }

body.android .container {
	width: 100%;
	height: 100vh;
	padding: 40px 0 80px 0; }
