.finish {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: $white;
	z-index: 100;

	&__content {
		@include vcenter;
		top: 40%; }

	&__title {
		@include fontRB;
		text-align: center;
		margin-bottom: 20px;
		margin-top: 40px; }

	&__code {
		padding: 20px;
		@include fontRM;
		font-size: 32px;
		border: 5px solid $blue;
		border-radius: 5px;
		margin-bottom: 40px;
		text-align: center; }

	&__text {
		@include fontRM;
		font-size: 20px;
		text-align: center; } }
