.form-create-member {
	$this: &;
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba($black, 0.5);
	z-index: 20;
	&.is-show {
		display: block; }
	& *.is-disabled {
		pointer-events: none; }
	&__content {
		@include vcenter;
		width: 800px;
		padding: 30px;
		background-color: $white;
		border: 2px solid $blue;
		border-radius: 5px; }

	&__wrap {
		display: flex;
		justify-content: center;
		margin-bottom: 25px; }

	&__flex {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 25px;
		&.is-disabled {
			opacity: 0.7;
			#{$this}__subtitle {
				color: $gray; } } }

	&__el {
		flex-grow: 1; }

	&__inner {
		flex-grow: 1;
		display: flex;
		justify-content: space-between; }

	&__date {
		flex-grow: 1;
		margin-right: 10px;
		&:last-child {
			margin-right: 0; } }

	&__age {
		@include fontRB;
		font-size: 18px; }

	&__subtitle {
		width: 240px;
		@include fontRM;
		font-size: 18px;
		text-transform: uppercase;
		margin-right: 10px;
		&--center {
			text-align: center;
			margin: 0 auto; } }

	&__radio {
		margin-right: 130px;
		&:last-child {
			margin-right: 0; } }

	&__btns {
		display: flex;
		justify-content: space-between; }

	&__btn {
		width: 230px; }

	&__close {
		position: absolute;
		top: 10px;
		right: 10px;
		display: flex;
		margin-left: auto;
		font-size: 0;
		width: 40px;
		height: 40px;
		border-radius: 50%;
		background-color: $transp;
		&:before, &:after {
			content: '';
			position: absolute;
			top: 5px;
			left: 19px;
			transform: rotate(45deg);
			width: 2px;
			height: 30px;
			background-color: $blue; }
		&:after {
			transform: rotate(-45deg); }
		&:hover {
			background-color: rgba($blue, 0.2); } }

	.input:disabled {
		opacity: 1;
		color: $gray; }
	.input::-webkit-inner-spin-button,
	.input::-webkit-outer-spin-button {
		-webkit-appearance: none; } }
