.indiv-header-houses {
	display: flex;
	justify-content: space-between;
	margin: 15px 0;
	padding: 0 30px;

	&__title {
		margin-bottom: 30px;
		@include fontRB;
		font-size: 26px;
		text-transform: uppercase; }
	&__text {
		margin-right: 20px; }

	&__row {
		display: flex;
		justify-content: space-between;
		align-items: center; }

	&__item {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		// &:nth-child(2)
		// 	position: relative
		// 	&:before, &:after
		// 		content: ''
		// 		position: absolute
		// 		top: 50%
		// 		left: -50px
		// 		transform: translateY(-50%)
		// 		height: 80%
		// 		width: 1px
		// 		background-color: $blue
		// 	&:after
		// 		left: auto
 }		// 		right: -50px

	& &__select {
		width: 300px;
		.selectize-input {
			display: flex;
			& > input {
				min-width: 100%!important;
				font-size: 14px;
				@include placeholder {
					font-size: 14px; } }
			&.input-active {
				display: flex; } }
		.selectize-input {
			height: 30px;
			line-height: 30px;
			&:before {
				width: 30px;
				height: 30px; }
			&:after {
				right: 8px;
				top: 5px; }
			&.dropdown-active:after {
				top: 10px; } }

		.selectize-dropdown {
			top: 30px!important; } } }
